font family to sans-serif.\n * 2. Prevent iOS text size adjust after orientation change, without disabling\n *    user zoom.\n */\n\nhtml {\n    font-family: sans-serif; /* 1 */\n    -ms-text-size-adjust: 100%; /* 2 */\n    -webkit-text-size-adjust: 100%; /* 2 */\n}\n\n/**\n * Remove default margin.\n */\n\nbody {\n    margin: 0;\n}\n\n/* HTML5 display definitions\n   ========================================================================== */\n\n/**\n * Correct `block` display not defined for any HTML5 element in IE 8/9.\n * Correct `block` display not defined for `details` or `summary` in IE 10/11\n * and Firefox.\n * Correct `block` display not defined for `main` in IE 11.\n */\n\narticle,\naside,\ndetails,\nfigcaption,\nfigure,\nfooter,\nheader,\nhgroup,\nmain,\nmenu,\nnav,\nsection,\nsummary {\n    display: block;\n}\n\n/**\n * 1. Correct `inline-block` display not defined in IE 8/9.\n * 2. Normalize vertical alignment of `progress` in Chrome, Firefox, and Opera.\n */\n\naudio,\ncanvas,\nprogress,\nvideo {\n    display: inline-block; /* 1 */\n    vertical-align: baseline; /* 2 */\n}\n\n/**\n * Prevent modern browsers from displaying `audio` without controls.\n * Remove excess height in iOS 5 devices.\n */\n\naudio:not([controls]) {\n    display: none;\n    height: 0;\n}\n\n/**\n * Address `[hidden]` styling not present in IE 8/9/10.\n * Hide the `template` element in IE 8/9/11, Safari, and Firefox < 22.\n */\n\n[hidden],\ntemplate {\n    display: none;\n}\n\n/* Links\n   ========================================================================== */\n\n/**\n * Remove the gray background color from active links in IE 10.\n */\n\na {\n    background-color: transparent;\n}\n\n/**\n * Improve readability when focused and also mouse hovered in all browsers.\n */\n\na:active,\na:hover {\n    outline: 0;\n}\n\n/* Text-level semantics\n   ========================================================================== */\n\n/**\n * Address styling not present in IE 8/9/10/11, Safari, and Chrome.\n */\n\nabbr[title] {\n    border-bottom: 1px dotted;\n}\n\n/**\n * Address style set to `bolder` in Firefox 4+, Safari, and Chrome.\n */\n\nb,\nstrong {\n    font-weight: bold;\n}\n\n/**\n * Address styling not present in Safari and Chrome.\n */\n\ndfn {\n    font-style: italic;\n}\n\n/**\n * Address variable `h1` font-size and margin within `section` and `article`\n * contexts in Firefox 4+, Safari, and Chrome.\n */\n\nh1 {\n    font-size: 2em;\n    margin: 0.67em 0;\n}\n\n/**\n * Address styling not present in IE 8/9.\n */\n\nmark {\n    background: #ff0;\n    color: #000;\n}\n\n/**\n * Address inconsistent and variable font size in all browsers.\n */\n\nsmall {\n    font-size: 80%;\n}\n\n/**\n * Prevent `sub` and `sup` affecting `line-height` in all browsers.\n */\n\nsub,\nsup {\n    font-size: 75%;\n    line-height: 0;\n    position: relative;\n    vertical-align: baseline;\n}\n\nsup {\n    top: -0.5em;\n}\n\nsub {\n    bottom: -0.25em;\n}\n\n/* Embedded content\n   ========================================================================== */\n\n/**\n * Remove border when inside `a` element in IE 8/9/10.\n */\n\nimg {\n    border: 0;\n}\n\n/**\n * Correct overflow not hidden in IE 9/10/11.\n */\n\nsvg:not(:root) {\n    overflow: hidden;\n}\n\n/* Grouping content\n   ========================================================================== */\n\n/**\n * Address margin not present in IE 8/9 and Safari.\n */\n\nfigure {\n    margin: 1em 40px;\n}\n\n/**\n * Address differences between Firefox and other browsers.\n */\n\nhr {\n    -moz-box-sizing: content-box;\n    box-sizing: content-box;\n    height: 0;\n}\n\n/**\n * Contain overflow in all browsers.\n */\n\npre {\n    overflow: auto;\n}\n\n/**\n * Address odd `em`-unit font size rendering in all browsers.\n */\n\ncode,\nkbd,\npre,\nsamp {\n    font-family: monospace, monospace;\n    font-size: 1em;\n}\n\n/* Forms\n   ========================================================================== */\n\n/**\n * Known limitation: by default, Chrome and Safari on OS X allow very limited\n * styling of `select`, unless a `border` property is set.\n */\n\n/**\n * 1. Correct color not being inherited.\n *    Known issue: affects color of disabled elements.\n * 2. Correct font properties not being inherited.\n * 3. Address margins set differently in Firefox 4+, Safari, and Chrome.\n */\n\nbutton,\ninput,\noptgroup,\nselect,\ntextarea {\n    color: inherit; /* 1 */\n    font: inherit; /* 2 */\n    margin: 0; /* 3 */\n}\n\n/**\n * Address `overflow` set to `hidden` in IE 8/9/10/11.\n */\n\nbutton {\n    overflow: visible;\n}\n\n/**\n * Address inconsistent `text-transform` inheritance for `button` and `select`.\n * All other form control elements do not inherit `text-transform` values.\n * Correct `button` style inheritance in Firefox, IE 8/9/10/11, and Opera.\n * Correct `select` style inheritance in Firefox.\n */\n\nbutton,\nselect {\n    text-transform: none;\n}\n\n/**\n * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`\n *    and `video` controls.\n * 2. Correct inability to style clickable `input` types in iOS.\n * 3. Improve usability and consistency of cursor style between image-type\n *    `input` and others.\n */\n\nbutton,\nhtml input[type=\"button\"], /* 1 */\ninput[type=\"reset\"],\ninput[type=\"submit\"] {\n    -webkit-appearance: button; /* 2 */\n    cursor: pointer; /* 3 */\n}\n\n/**\n * Re-set default cursor for disabled elements.\n */\n\nbutton[disabled],\nhtml input[disabled] {\n    cursor: default;\n}\n\n/**\n * Remove inner padding and border in Firefox 4+.\n */\n\nbutton::-moz-focus-inner,\ninput::-moz-focus-inner {\n    border: 0;\n    padding: 0;\n}\n\n/**\n * Address Firefox 4+ setting `line-height` on `input` using `!important` in\n * the UA stylesheet.\n */\n\ninput {\n    line-height: normal;\n}\n\n/**\n * It's recommended that you don't attempt to style these elements.\n * Firefox's implementation doesn't respect box-sizing, padding, or width.\n *\n * 1. Address box sizing set to `content-box` in IE 8/9/10.\n * 2. Remove excess padding in IE 8/9/10.\n */\n\ninput[type=\"checkbox\"],\ninput[type=\"radio\"] {\n    box-sizing: border-box; /* 1 */\n    padding: 0; /* 2 */\n}\n\n/**\n * Fix the cursor style for Chrome's increment/decrement buttons. For certain\n * `font-size` values of the `input`, it causes the cursor style of the\n * decrement button to change from `default` to `text`.\n */\n\ninput[type=\"number\"]::-webkit-inner-spin-button,\ninput[type=\"number\"]::-webkit-outer-spin-button {\n    height: auto;\n}\n\n/**\n * 1. Address `appearance` set to `searchfield` in Safari and Chrome.\n * 2. Address `box-sizing` set to `border-box` in Safari and Chrome\n *    (include `-moz` to future-proof).\n */\n\ninput[type=\"search\"] {\n    -webkit-appearance: textfield; /* 1 */\n    -moz-box-sizing: content-box;\n    -webkit-box-sizing: content-box; /* 2 */\n    box-sizing: content-box;\n}\n\n/**\n * Remove inner padding and search cancel button in Safari and Chrome on OS X.\n * Safari (but not Chrome) clips the cancel button when the search input has\n * padding (and `textfield` appearance).\n */\n\ninput[type=\"search\"]::-webkit-search-cancel-button,\ninput[type=\"search\"]::-webkit-search-decoration {\n    -webkit-appearance: none;\n}\n\n/**\n * Define consistent border, margin, and padding.\n */\n\nfieldset {\n    border: 1px solid #c0c0c0;\n    margin: 0 2px;\n    padding: 0.35em 0.625em 0.75em;\n}\n\n/**\n * 1. Correct `color` not being inherited in IE 8/9/10/11.\n * 2. Remove padding so people aren't caught out if they zero out fieldsets.\n */\n\nlegend {\n    border: 0; /* 1 */\n    padding: 0; /* 2 */\n}\n\n/**\n * Remove default vertical scrollbar in IE 8/9/10/11.\n */\n\ntextarea {\n    overflow: auto;\n}\n\n/**\n * Don't inherit the `font-weight` (applied by a rule above).\n * NOTE: the default cannot safely be changed in Chrome and Safari on OS X.\n */\n\noptgroup {\n    font-weight: bold;\n}\n\n/* Tables\n   ========================================================================== */\n\n/**\n * Remove most spacing between table cells.\n */\n\ntable {\n    border-collapse: collapse;\n    border-spacing: 0;\n}\n\ntd,\nth {\n    padding: 0;\n}\n\n*, *:after, *:before {\n    box-sizing: border-box;\n}\n","// --------------------------------------------------\n// Plugins (Third-Party-Libraries)\n// --------------------------------------------------\n@import \"../src/novo-elements\";\n\n// --------------------------------------------------\n// Global\n// --------------------------------------------------\n@import \"assets/styles/reset\";\n@import \"assets/styles/general\";\n@import \"assets/styles/main\";\n@import \"assets/styles/sidenav\";\n@import \"assets/styles/highlight\";\n@import \"assets/styles/media\";\n\n// --------------------------------------------------\n// Pages\n// --------------------------------------------------\n@import \"pages/color/Color\";\n@import \"pages/home/Home\";\n@import \"pages/iconography/Iconography\";\n@import \"pages/typography/Typography\";\n@import \"pages/layout/Layout\";\n@import \"pages/button/ButtonDemo\";\n@import \"pages/tabs/TabsDemo\";\n@import \"pages/toast/ToastDemo\";\n@import \"pages/switch/SwitchDemo\";\n@import \"pages/select/SelectDemo\";\n@import \"pages/dropdown/DropdownDemo\";\n@import \"pages/loading/LoadingDemo\";\n@import \"pages/card/CardDemo\";\n@import \"pages/pipes/PipesDemo\";\n@import \"pages/tooltip/TooltipDemo\";\n@import \"pages/header/HeaderDemo\";\n\n// --------------------------------------------------\n// Shameful CSS is unrefactored, quick fixes.\n// Ideally nothing is in this file - but it's better\n// than mucking up other stylesheets without being\n// totally sure it's a good idea\n// --------------------------------------------------\n@import \"assets/styles/shame\";\n","// Colors\n// --------------------------------------------------\n$black: #000000;\n$white: #ffffff;\n$grey: #999999;\n$off-white: #f4f4f4;\n$dark: #3D464D;\n$light: #D9DADC;\n$navy: #0D2D42;\n$pulse: #3BAFDA;\n\n// Analytics Colors\n$aqua: #3BAFDA;\n$ocean: #4A89DC;\n$mint: #37BC9B;\n$grass: #8CC152;\n$sunflower: #F6B042;\n$bittersweet: #EB6845;\n$grapefruit: #DA4453;\n$carnation: #D770AD;\n$lavender: #967ADC;\n\n// Entity Colors\n$company: #3399DD;\n$candidate: #44BB77;\n$navigation: #2F384F;\n$lead: #AA6699;\n$contact: #FFAA44;\n$opportunity: #662255;\n$job: #BB5566;\n$submission: #778899;\n$placement: #0B344F;\n\n// Color Applications\n$positive: $ocean;\n$success: $grass;\n$negative: $grapefruit;\n$warning: $sunflower;\n$background: $off-white;\n$background-dark: #E2E2E2;\n\n/*\n===================\n    Color Maps\n===================\n*/\n\n// Analytics Color Map\n$analytics-colors: (\n    aqua: $aqua,\n    ocean: $ocean,\n    mint: $mint,\n    grass: $grass,\n    sunflower: $sunflower,\n    bittersweet: $bittersweet,\n    grapefruit: $grapefruit,\n    carnation: $carnation,\n    lavender: $lavender\n);\n\n// Entity Color Map\n$entity-colors: (\n    company: $company,\n    candidate: $candidate,\n    navigation: $navigation,\n    lead: $lead,\n    contact: $contact,\n    opportunity: $opportunity,\n    job: $job,\n    submission: $submission,\n    placement: $placement\n);\n\n// Generic Color Map\n$colors: (\n    positive: $ocean,\n    success: $grass,\n    negative: $grapefruit,\n    warning: $sunflower,\n    background: $off-white,\n    black: $black,\n    white: $white,\n    grey: $grey,\n    off-white: $off-white,\n    dark: $dark,\n    light: $light,\n    pulse: $pulse\n);\n\n// Loading Dot Color Map\n$dot-colors: (\n    lavender: $lavender,\n    grapefruit: $grapefruit,\n    sunflower: $sunflower,\n    mint: $mint,\n    ocean: $ocean\n);\n\n// Typography\n// -------------------------\n$base-font-size: 14px;\n$base-font-family: \"Roboto\", sans-serif;\n$base-font-color: $dark;\n$base-line-height: 24px;\n$alt-font-family: Georgia, \"Times New Roman\", Times, serif;\n\n// Whiteframes\n// -------------------------\n$whiteframe-shadow-z1: 0px 2px 5px 0 rgba(0, 0, 0, 0.26) !default;\n$whiteframe-zindex-z1: 1 !default;\n$whiteframe-shadow-z2: 0px 8px 17px rgba(0, 0, 0, 0.2) !default;\n$whiteframe-zindex-z2: 2 !default;\n$whiteframe-shadow-z3: 0px 17px 50px rgba(0, 0, 0, 0.19) !default;\n$whiteframe-zindex-z3: 3 !default;\n$whiteframe-shadow-z4: 0px 16px 28px 0 rgba(0, 0, 0, 0.22) !default;\n$whiteframe-zindex-z4: 4 !default;\n$whiteframe-shadow-z5: 0px 27px 24px 0 rgba(0, 0, 0, 0.2) !default;\n$whiteframe-zindex-z5: 5 !default;\n\n// Z-indexes\n// -------------------------\n\n// Function to call z-indexes.\n// Usage: z-index: z('modal');\n\n@function z($layer) {\n    $z-layers: (\n        \"goku\": 9001,\n        \"modal\": 500,\n        \"tooltip\": 100,\n        \"toast\": 90,\n        \"dialogue\": 80,\n        \"bottom-sheet\": 70,\n        \"sidenav\": 60,\n        \"backdrop\": 50,\n        \"fab\": 20,\n        \"default\": 1,\n        \"below\": -1,\n        \"bottomless-pit\": -9000\n    );\n\n    @if not map-has-key($z-layers, $layer) {\n        @warn \"No layer found for `#{$layer}` in $z-layers map. Property omitted.\";\n    }\n\n    @return map-get($z-layers, $layer);\n}\n\n// Easing Curves\n// -------------------------\n// Usage: html-tag { @include ease('all', 'swift-ease-in-out'); }\n\n$easing-curves: (\n    \"swift-ease-out\":(\n        \"duration\": 400ms,\n        \"curve\": \"cubic-bezier(0.25, 0.8, 0.25, 1)\"\n    ),\n    \"swift-ease-in\": (\n        \"duration\": 300ms,\n        \"curve\": \"cubic-bezier(0.55, 0, 0.55, 0.2)\"\n    ),\n    \"swift-ease-in-out\": (\n        \"duration\": 500ms,\n        \"curve\": \"cubic-bezier(0.35, 0, 0.25, 1)\"\n    )\n);\n\n@function map-deep-get($map, $keys...) {\n    @each $key in $keys {\n        $map: map-get($map, $key);\n    }\n\n    @return $map;\n}\n\n@mixin ease($target, $curve-name) {\n    $duration: map-deep-get($easing-curves, $curve-name, \"duration\");\n    $curve: map-deep-get($easing-curves, $curve-name, \"curve\");\n    transition: #{$target $duration $curve};\n}\n\n// Animations\n// -------------------------\n@keyframes rotate {\n    0% {\n        transform: rotateZ(0deg);\n    }\n    75% {\n        transform: rotateZ(200deg);\n    }\n    100% {\n        transform: rotateZ(180deg);\n    }\n}\n\n@keyframes half-rotate {\n    0% {\n        transform: rotateZ(45deg);\n    }\n    75% {\n        transform: rotateZ(100deg);\n    }\n    100% {\n        transform: rotateZ(90deg);\n    }\n}\n\n@keyframes rotateBack {\n    0% {\n        transform: rotateZ(90deg);\n    }\n    100% {\n        transform: rotateZ(0deg);\n    }\n}\n\n@keyframes show {\n    0% {\n        opacity: 0;\n        transform: translateX(-100%);\n    }\n    75% {\n        transform: translateX(0px);\n    }\n    100% {\n        opacity: 1;\n        transform: translateX(0);\n    }\n}\n","/**\n * Flex Grid -\n * Version: 0.3.2\n *\n * Simple grid built with flex box and sass.\n *\n * Matthew Simo - matthew.a.simo@gmail.com\n */\n\n/**\n * Grid setup\n *\n * The grid will calculate dimensions based on these two variables:\n * $fg-columns will inform the grid loops how many columns there should be.\n * $fg-gutter will inform the grid loops how big eac column's gutters should be.\n *\n * The grid will name columns, rows, offsets based on these three variables:\n * $fg-class-row string used for the row class\n * $fg-class-col string used for the column class\n * $fg-class-off string used for the offset class\n */\n$fg-columns: 12 !default;\n$fg-gutter: 0.5rem !default;\n$fg-class-row: 'row' !default;\n$fg-class-col: 'col' !default;\n$fg-class-off: 'off' !default;\n\n/**\n * Break point namespace object\n *\n * Set the default namespace object with these defaults with the\n * understanding that you can pass in whatever you might require for your site.\n *\n * $fg-breakpoints is a Sass list with nested lists inside. Each sub list defines two things.\n * 1. The namespace for that breakpoint. (Required) (i.e. xs, sm, md, lg)\n * 2. The min-width measurement for the breakpoint for that namespace. (i.e. 48em, 62em, 75em)\n *\n * Note: These should be in the proper order (at least till libsass handles map keys properly).\n *\n * Note: If the measurement is left out then it will be skipped when generating\n * the grid and applied to global styles.\n *\n */\n$fg-breakpoints: ((xs), (sm, 768px), (md, 992px), (lg, 1200px)) !default;\n\n/**\n * Class Name Defaults\n *\n * Define class names for columns, rows and offsets in case compatibility with other\n * libraries is necessary.\n\n/**\n * Calculate column size percentage\n */\n@function get-col-percent($column-num) {\n    @return $column-num / $fg-columns * 100%;\n}\n\n/**\n * Spacing mixin to create uniform margin/padding\n */\n@mixin spacing( $value, $type: margin, $orientation: vertical ) {\n    @if $orientation == vertical {\n        #{ $type }-top: $value;\n        #{ $type }-bottom: $value;\n    } @else {\n        #{ $type }-left: $value;\n        #{ $type }-right: $value;\n    }\n}\n\n/**\n * Row wrapper class, flex box parent.\n */\n.#{ $fg-class-row } {\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n    @include spacing((-$fg-gutter), margin, horizontal);\n}\n\n%#{ $fg-class-col } {\n    box-sizing: border-box;\n    display: flex;\n    flex-direction: column;\n    flex-grow: 0;\n    flex-shrink: 0;\n    @include spacing(($fg-gutter), padding, horizontal);\n}\n\n%#{ $fg-class-col }-flex {\n    @extend %#{ $fg-class-col };\n    flex-grow: 1;\n    flex-basis: 0;\n    max-width: 100%;\n}\n\n/**\n * Generate a set of grid column classes using a namespace\n *\n * .#{ $fg-class-col }-[namespace] for intelligent column division\n * .#{ $fg-class-col }-[namespace]-[number] for a column that covers a specific number of columns (e.g. 1-12 by default)\n * .#{ $fg-class-off }-[namespace]-[number] for pushing a col a specific number of columns (e.g. 1-11 by default)\n */\n@mixin grid-base( $namespace ) {\n    .#{ $fg-class-col }-#{$namespace} {\n        @extend %#{ $fg-class-col }-flex;\n    }\n    @for $i from 1 through $fg-columns {\n        .#{ $fg-class-col }-#{$namespace}-#{$i} {\n            @extend %#{ $fg-class-col };\n        }\n    }\n}\n\n@mixin grid-sizes( $namespace ) {\n    // Run the loop for as many columns as speficied\n    @for $i from 1 through $fg-columns {\n        .#{ $fg-class-col }-#{$namespace}-#{$i} {\n            flex-basis: get-col-percent($i);\n            max-width: get-col-percent($i);\n        }\n    }\n    @for $i from 1 through $fg-columns {\n        // Don't include the .off class for the last column\n        @if ($i != $fg-columns) {\n            .#{ $fg-class-off }-#{$namespace}-#{$i} {\n                margin-left: get-col-percent($i);\n            }\n        }\n    }\n}\n\n/**\n * Build the grid in two steps, to help minimize file size\n * Step 1, for each namespace, create the grid-base\n * Step 2, for each namespace, wrap the col width/offset measurements in their breakpoint media query\n */\n@each $val in $fg-breakpoints {\n    $namespace: nth($val, 1);\n    @include grid-base($namespace);\n}\n\n@each $val in $fg-breakpoints {\n    @if length($val) == 1 {\n        $namespace: nth($val, 1);\n        @include grid-sizes($namespace);\n    } @else {\n        $namespace: nth($val, 1);\n        $size: nth($val, 2);\n        @media only screen and (min-width: #{$size}) {\n            @include grid-sizes($namespace);\n        }\n    }\n}\n","// hint.scss\n//\n// Aggregates all other sass files.\n\n/*-------------------------------------*\\\n\tHINT.css - A CSS tooltip library\n\\*-------------------------------------*/\n\n\n/**\n * HINT.css is a tooltip library made in pure CSS.\n *\n * Source: https://github.com/chinchang/hint.css\n * Demo: http://kushagragour.in/lab/hint/\n *\n * Release under The MIT License\n *\n */\n\n@import \"hint-variables\";\n@import \"hint-mixins\";\n@import \"hint-core\";\n@import \"hint-position\";\n@import \"hint-sizes\";\n@import \"hint-theme\";\n@import \"hint-color-types\";\n@import \"hint-always\";\n@import \"hint-rounded\";\n@import \"hint-effects\";\n","/**\n * source: hint-core.scss\n *\n * Defines the basic styling for the tooltip.\n * Each tooltip is made of 2 parts:\n * \t1) body (:after)\n * \t2) arrow (:before)\n *\n * Classes added:\n * \t1) hint\n */\n\n[data-hint] {\n\tposition: relative;\n\tdisplay: inline-block;\n\n\t&:before, &:after {\n\t\tposition: absolute;\n\n\t\t// HACK: Trigger hardware accelerated rendering, otherwise transform was not\n\t\t// working on a hidden element\n\t\t@include vendor('transform', translate3d(0, 0, 0));\n\n\t\t// HACK: visibility is set to hidden because IE & Opera don't support\n\t\t// pointer-events on HTML content yet because of which hovering a hidden tooltip\n\t\t// shows the tooltip.\n\t\tvisibility: hidden;\n\t\topacity: 0;\n\t\tz-index: $hintZIndex;\n\t\t// shouldn't receive pointer events, otherwise even hovering tooltip will make it appear\n\t\tpointer-events: none;\n\n\t\t@include vendor('transition', 0.3s ease);\n\t\t@include vendor('transition-delay', $hintHideDelay);\n\t}\n\n\t&:hover:before, &:hover:after {\n\t\tvisibility: visible;\n\t\topacity: 1;\n\t}\n\n\t&:hover:before, &:hover:after {\n\t\t// $hintShowDelay will apply as soon as element is hovered.\n\t\t@include vendor('transition-delay', $hintShowDelay);\n\t}\n\n\t/**\n\t * tooltip arrow\n\t */\n\t&:before {\n\t\tcontent: '';\n\t\tposition: absolute;\n\t\tbackground: transparent;\n\t\tborder: $hintArrowBorderWidth solid transparent;\n\t\t// move z-index 1 up than :after so that it shows over box-shadow\n\t\tz-index: $hintZIndex + 1;\n\t}\n\n\t/**\n\t * tooltip body\n\t */\n\t&:after {\n\t\tcontent: attr(data-hint); // The magic!\n\t\tbackground: $hintDefaultColor;\n\t\tcolor: white;\n\t\tpadding: $hintVerticalPadding $hintHorizontalPadding;\n\t\tfont-size: $hintFontSize;\n\t\tfont-family: $hintFontFamily;\n\t\tline-height: $hintFontSize; // Vertical centering.\n\t\twhite-space: nowrap; // Prevent breaking to new line.\n\t}\n}\n\n[data-hint=''] {\n\t&:before, &:after {\n\t\tdisplay: none !important;\n\t}\n}\n","// hint-mixins.scss\n//\n// Place to store common mixins.\n\n// Vendor prefixer mixin.\n@mixin vendor($property, $value) {\n\t-webkit-#{$property}: $value;\n\t-moz-#{$property}: $value;\n\t#{$property}: $value;\n}\n\n// Generates border-color rules for all possible positions\n@mixin arrow-border-color($color) {\n\t@each $position in top, bottom, left, right {\n\t\t@if $position == top or $position == bottom {\n\t\t\t// Loop further for classes like .top-left, bottom-right etc\n\t\t\t@each $xDir in left, right {\n\t\t\t\t&.#{$hintPrefix}#{$position}-#{$xDir}:before {\n\t\t\t\t\tborder-#{$position}-color: $color;\n\t\t\t\t}\n\t\t\t}\n\t\t}\n\t\t&.#{$hintPrefix}#{$position}:before {\n\t\t\tborder-#{$position}-color: $color;\n\t\t}\n\t}\n}\n\n// mixin to set margin on tooltip using translate transform\n// $property\n@mixin set-margin($property, $transitionDirection, $translateX: 0) {\n\t$value: unquote(\"#{$property}(#{$hintTransitionDistance * $transitionDirection})\");\n\t&:before {\n\t\t@include vendor('transform', $value);\n\t}\n\t&:after {\n\t\t@if $translateX != 0 {\n\t\t\t// For vertical tooltips, we need to animate in y-direction\n\t\t\t// retaining its x-transform.\n\t\t\t@include vendor('transform', translateX($translateX) $value);\n\t\t}\n\t\t@else {\n\t\t\t@include vendor('transform', $value);\n\t\t}\n\t}\n}\n","// hint-variables.scss\n//\n// Declares some variables used within the library.\n\n// Prefix for all classes. By default, BEM naming convention is used\n$hintPrefix: 'hint--' !default;\n\n// font size\n$hintFontSize: 12px !default;\n\n// default font family\n$hintFontFamily: 'Helvetica Neue', Helvetica, Arial, sans-serif !default;\n\n// paddings\n$hintVerticalPadding: 8px !default;\n$hintHorizontalPadding: 10px !default;\n\n// default tooltip height\n$hintTooltipHeight: $hintFontSize + 2 * $hintVerticalPadding !default;\n\n// border-width for tooltip arrow\n$hintArrowBorderWidth: 6px !default;\n\n// horizontal arrow offset\n$hintArrowOffsetX: 2 * $hintArrowBorderWidth !default;\n\n// text-shadow darken percentage\n$hintTextShadowDarkenAmount: 25% !default;\n\n// transition distance\n$hintTransitionDistance: 8px !default;\n\n// Delay in showing the tooltips.\n$hintShowDelay: 100ms !default;\n\n// Delay in hiding the tooltips.\n$hintHideDelay: 0ms !default;\n\n// z-index for tooltips\n$hintZIndex: 1000000 !default;\n\n// Size options\n$hintSizeSmall: 80px !default;\n$hintSizeMedium: 150px !default;\n$hintSizeLarge: 300px !default;\n\n// Various colors\n// Default color is blackish\n$hintDefaultColor: hsl(0, 0%, 22%) !default;\n\n// Error color\n$hintErrorColor: hsl(1, 40%, 50%) !default;\n\n// Warning color\n$hintWarningColor: hsl(38, 46%, 54%) !default;\n\n// Info Color\n$hintInfoColor: hsl(200, 50%, 45%) !default;\n\n// Success Color\n$hintSuccessColor: hsl(121, 32%, 40%) !default;\n","/**\n * source: hint-position.scss\n *\n * Defines the positoning logic for the tooltips.\n *\n * Classes added:\n * \t1) hint--top\n * \t2) hint--bottom\n * \t3) hint--left\n * \t4) hint--right\n */\n\n@mixin vertical-positioned-tooltip($propertyY, $transitionDirection, $xDirection:0) {\n\t&:before {\n\t\t// bring arrow inside so that it animates to normal position when shown.\n\t\t// HACK: +1px to avoid the 1 px white space between arrow and body during transition.\n\t\tmargin-#{$propertyY}: -2 * $hintArrowBorderWidth + 1px;\n\t}\n\n\t&:before, &:after {\n\t\t#{$propertyY}: 100%;\n\t\tleft: 50%; // get top-left corner in center\n\t}\n\n\t&:before {\n\t\tleft: calc(50% - #{$hintArrowBorderWidth}); // get arrow center aligned with content\n\t}\n\n\t$translateX: -50%;\n\t@if $xDirection == -1 {\n\t\t$translateX: -100%;\n\t}\n\t@elseif $xDirection == 1 {\n\t\t$translateX: 0;\n\t}\n\n\t&:after {\n\t\t@include vendor('transform', translateX($translateX));\n\t}\n\n\t&:after {\n\t\t@if $xDirection != 0 {\n\t\t\t// bring back the tooltip by some offset so that arrow doesn't stick at end\n\t\t\tmargin-left: -$xDirection * $hintArrowOffsetX;\n\t\t}\n\t}\n\n\t&:hover, &:focus {\n\t\t@include set-margin('translateY', $transitionDirection, $translateX);\n\t}\n}\n\n@mixin horizontal-positioned-tooltip($propertyX, $transitionDirection) {\n\t&:before {\n\t\t// bring arrow inside so that it animates to normal position when shown\n\t\t// HACK: +1px to avoid the 1 px white space between arrow and body during transition.\n\t\tmargin-#{$propertyX}: -2 * $hintArrowBorderWidth + 1px;\n\t\t// bring back to center vertically\n\t\tmargin-bottom: -1 * $hintArrowBorderWidth;\n\t}\n\n\t&:after {\n\t\t// bring back to center\n\t\tmargin-bottom: -1 * floor($hintTooltipHeight / 2);\n\t}\n\n\t&:before, &:after {\n\t\t#{$propertyX}: 100%;\n\t\tbottom: 50%;\n\t}\n\n\t&:hover, &:focus {\n\t\t@include set-margin('translateX', $transitionDirection);\n\t}\n}\n\n\n/**\n * set default color for tooltip arrows\n */\n@include arrow-border-color($hintDefaultColor);\n\n/**\n * top tooltip\n */\n.#{$hintPrefix}top {\n\t@include vertical-positioned-tooltip('bottom', -1);\n}\n\n/**\n * bottom tooltip\n */\n.#{$hintPrefix}bottom {\n\t@include vertical-positioned-tooltip('top', 1);\n}\n\n/**\n * right tooltip\n */\n.#{$hintPrefix}right {\n\t@include horizontal-positioned-tooltip('left', 1);\n}\n\n/**\n * left tooltip\n */\n.#{$hintPrefix}left {\n\t@include horizontal-positioned-tooltip('right', -1);\n}\n\n/**\n * top-left tooltip\n */\n.#{$hintPrefix}top-left {\n\t@include vertical-positioned-tooltip('bottom', -1, -1);\n}\n\n\n/**\n * top-right tooltip\n */\n.#{$hintPrefix}top-right {\n\t@include vertical-positioned-tooltip('bottom', -1, 1);\n}\n\n/**\n * bottom-left tooltip\n */\n.#{$hintPrefix}bottom-left {\n\t@include vertical-positioned-tooltip('top', 1, -1);\n}\n\n\n/**\n * bottom-right tooltip\n */\n.#{$hintPrefix}bottom-right {\n\t@include vertical-positioned-tooltip('top', 1, 1);\n}","/**\n * source: hint-sizes.scss\n *\n * Defines width restricted tooltips that can span\n * across multiple lines.\n *\n * Classes added:\n * \t1) hint--small\n * \t2) hint--medium\n * \t3) hint--large\n *\n */\n\n\n.#{$hintPrefix}small,\n.#{$hintPrefix}medium,\n.#{$hintPrefix}large {\n\t&:after {\n\t\twhite-space: normal;\n\t\tline-height: 1.4em;\n\t}\n}\n\n.#{$hintPrefix}small {\n\t&:after {\n\t\twidth: $hintSizeSmall;\n\t}\n}\n.#{$hintPrefix}medium {\n\t&:after {\n\t\twidth: $hintSizeMedium;\n\t}\n}\n.#{$hintPrefix}large {\n\t&:after {\n\t\twidth: $hintSizeLarge;\n\t}\n}","/**\n * source: hint-theme.scss\n *\n * Defines basic theme for tooltips.\n *\n */\n\n[data-hint] {\n\t/**\n\t * tooltip body\n\t */\n\t&:after {\n\t\ttext-shadow: 0 -1px 0px darken($hintDefaultColor, $hintTextShadowDarkenAmount);\n\t\tbox-shadow: 4px 4px 8px rgba(0, 0, 0, 0.3);\n\t}\n}","/**\n * source: hint-color-types.scss\n *\n * Contains tooltips of various types based on color differences.\n *\n * Classes added:\n * \t1) hint--error\n * \t2) hint--warning\n * \t3) hint--info\n * \t4) hint--success\n *\n */\n\n\n// mixin to generate different color style tooltips\n@mixin hint-type($color) {\n\t&:after {\n\t\tbackground-color: $color;\n\t\ttext-shadow: 0 -1px 0px darken($color, $hintTextShadowDarkenAmount);\n\t}\n\n\t// generate arrow color style\n\t@include arrow-border-color($color);\n}\n\n/**\n * Error\n */\n.#{$hintPrefix}error {\n\t@include hint-type($hintErrorColor);\n}\n\n/**\n * Warning\n */\n.#{$hintPrefix}warning {\n\t@include hint-type($hintWarningColor)\n}\n\n/**\n * Info\n */\n.#{$hintPrefix}info {\n\t@include hint-type($hintInfoColor)\n}\n\n/**\n * Success\n */\n.#{$hintPrefix}success {\n\t@include hint-type($hintSuccessColor)\n}\n","/**\n * source: hint-always.scss\n *\n * Defines a persisted tooltip which shows always.\n *\n * Classes added:\n * \t1) hint--always\n *\n */\n\n.#{$hintPrefix}always {\n\t&:after, &:before {\n\t\topacity: 1;\n\t\tvisibility: visible;\n\t}\n\n\t&.#{$hintPrefix}top {\n\t\t@include set-margin('translateY', -1, -50%);\n\n\t\t&-left {\n\t\t\t@include set-margin('translateY', -1, -100%);\n\t\t}\n\t\t&-right {\n\t\t\t@include set-margin('translateY', -1, 0);\n\t\t}\n\t}\n\n\t&.#{$hintPrefix}bottom {\n\t\t@include set-margin('translateY', 1, -50%);\n\t\t&-left {\n\t\t\t@include set-margin('translateY', 1, -100%);\n\t\t}\n\t\t&-right {\n\t\t\t@include set-margin('translateY', 1, 0);\n\t\t}\n\t}\n\n\t&.#{$hintPrefix}left {\n\t\t@include set-margin('translateX', -1);\n\t}\n\n\t&.#{$hintPrefix}right {\n\t\t@include set-margin('translateX', 1);\n\t}\n}\n\n","/**\n * source: hint-rounded.scss\n *\n * Defines rounded corner tooltips.\n *\n * Classes added:\n * \t1) hint--rounded\n *\n */\n\n.#{$hintPrefix}rounded {\n\t&:after {\n\t\tborder-radius: 4px;\n\t}\n}\n","/**\n * source: hint-effects.scss\n *\n * Defines various transition effects for the tooltips.\n *\n * Classes added:\n * \t1) hint--no-animate\n * \t2) hint--bounce\n *\n */\n\n// Remove animation from tooltips.\n.#{$hintPrefix}no-animate {\n\t&:before, &:after {\n\t\t@include vendor('transition-duration', 0ms);\n\t}\n}\n\n// Bounce effect in tooltips.\n.#{$hintPrefix}bounce {\n\t&:before, &:after {\n\t\t-webkit-transition: opacity 0.3s ease, visibility 0.3s ease, -webkit-transform 0.3s cubic-bezier(.71,1.7,.77,1.24);\n\t\t-moz-transition: opacity 0.3s ease, visibility 0.3s ease, -moz-transform 0.3s cubic-bezier(.71,1.7,.77,1.24);\n\t\ttransition: opacity 0.3s ease, visibility 0.3s ease, transform 0.3s cubic-bezier(.71,1.7,.77,1.24);\n\t}\n}\n","// HTML Root\n// --------------------------------------------------\n\nhtml {\n    font-family: $base-font-family;\n    font-size: $base-font-size;\n    color: $base-font-color;\n}\n\n// Headers\n// --------------------------------------------------\n\nh1 {\n    font-size: 2.5em;\n    font-weight: 400;\n    margin: 0;\n    padding: 0.45em 0 0.35em;\n    max-width: 33em;\n}\n\nh2 {\n    font-size: 2em;\n    font-weight: 500;\n    margin: 0;\n    padding: 0.75em 0 0.3em;\n    max-width: 33em;\n}\n\nh3 {\n    font-size: 1.75em;\n    font-weight: 300;\n    margin: 0;\n    padding: 0.6em 0 0.4em;\n    max-width: 33em;\n}\n\nh4 {\n    font-size: 1.375em;\n    font-weight: 400;\n    margin: 0;\n    padding: 0.75em 0 0.5em;\n    max-width: 33em;\n}\n\nh5 {\n    font-size: 1.125em;\n    font-weight: 700;\n    margin: 0;\n    padding: 0.75em 0 0.25em;\n    text-transform: uppercase;\n    color: rgba(0, 0, 0, .45);\n    max-width: 33em;\n}\n\nh6 {\n    font-size: 1.125em;\n    font-weight: 500;\n    margin: 0;\n    padding: 0.75em 0 0.25em;\n    max-width: 33em;\n}\n\n// Body And Paragraph\n// --------------------------------------------------\n\np {\n    font-size: 1rem;\n    line-height: 1.7rem;\n    margin: 0;\n    padding: 0.25rem 0 0.55rem;\n    max-width: 33em;\n}\n\n// Special Classes\n// --------------------------------------------------\n\n.caption {\n    font-size: 1.125em;\n    font-weight: 700;\n    max-width: 33em;\n    text-transform: uppercase;\n    opacity: 0.55;\n    letter-spacing: 0.1px;\n}\n\n// Iconography\n// --------------------------------------------------\n$icon-height: 1.7em; // 24px Base background size\n$icon-width: 1.7em; // 24px Base background size\n$base-icon-size: 1.4em; //18px Base font-size\n\ni {\n    font-size: $base-icon-size;\n    display: inline-block;\n\n    &[theme] {\n        &[theme=\"contained\"] {\n            font-size: 0.8em;\n            border-radius: 0.55em;\n            height: $icon-height;\n            width: $icon-width;\n            text-align: center;\n\n            &:before {\n                vertical-align: baseline;\n                line-height: 1.8;\n            }\n\n            @each $entity,\n            $color in $entity-colors {\n                &.#{$entity} {\n                    color: $white;\n                    background: $color;\n                }\n            }\n\n            @each $key,\n            $color in $analytics-colors {\n                &.#{$key} {\n                    color: $white;\n                    background: $color;\n                }\n            }\n        }\n\n        &[theme=\"entity\"] {\n            @each $entity,\n            $color in $entity-colors {\n                &.#{$entity} {\n                    color: $color;\n                    background: transparent;\n                }\n            }\n        }\n    }\n}\n\n// Nested icons\nh1,\nh2,\nh3,\nh4,\nh5,\nh6,\np,\nspan,\nfigcaption {\n    i {\n        margin: 0 0.45em 0 0;\n        padding: 0;\n\n        &:before {\n            vertical-align: middle !important;\n        }\n\n        &[theme] {\n            &[theme=\"contained\"] {\n                font-size: 0.8em;\n                vertical-align: 0.1em;\n\n                &:before {\n                    vertical-align: baseline;\n                    line-height: $icon-width;\n                    display: block;\n                }\n            }\n        }\n    }\n}\n\n// Hyperlinks\n// --------------------------------------------------\n\na {\n    color: $positive;\n    font-weight: 600;\n    text-decoration: none;\n    cursor: pointer;\n    transition: all 200ms ease-in-out;\n\n    &:visited {\n        color: $positive;\n    }\n\n    &:hover,\n    &:active {\n        color: darken($positive, 10%);\n    }\n}\n\n// Code\n// --------------------------------------------------\n\ncode {\n    font-size: 0.8em;\n    background: #F8F8F8;\n    border: 1px solid #E9EAEA;\n    color: #4C545A;\n    padding: 2px 4px;\n}\n","novo-nav {\n    background: rgba(0, 0, 0, .15);\n    padding: 0;\n    list-style: none;\n    display: flex;\n    //width: 100%;\n    align-items: baseline;\n    justify-content: flex-start;\n    margin: 0;\n\n    novo-tab,\n    novo-tab-link {\n        display: flex;\n        flex-direction: column;\n        align-items: center;\n\n        .novo-tab-link {\n            padding: 10px 20px;\n            color: #fff;\n            opacity: 0.65;\n            font-weight: 400;\n            cursor: pointer;\n            text-transform: uppercase;\n        }\n\n        .indicator {\n            width: 0;\n            height: 2px;\n            display: block;\n            background: transparent;\n            transition: all 220ms ease-in-out;\n        }\n\n        &.active,\n        &.router-link-active {\n            .novo-tab-link {\n                opacity: 1;\n            }\n\n            .indicator {\n                background: #fff;\n                width: 100%;\n            }\n        }\n\n        &:hover {\n            .novo-tab-link {\n                opacity: 1;\n            }\n        }\n    }\n\n    &[type=\"button-bar\"] {\n        display: inline-flex;\n        border-radius: 3px;\n        border: 2px solid #fff;\n\n        novo-tab-button {\n            padding: 15px 20px;\n            cursor: pointer;\n            transition: all 120ms ease-in-out;\n        }\n    }\n\n    &[theme=\"color\"] {\n        background: rgba(0, 0, 0, .2);\n\n        novo-tab,\n        novo-tab-link {\n            .novo-tab-link {\n                color: #fff;\n            }\n\n            &.active,\n            &.router-link-active {\n                .indicator {\n                    background: #fff;\n                }\n            }\n        }\n\n        &[type=\"button-bar\"] {\n            border: 2px solid #fff;\n\n            novo-tab-button {\n                color: rgba(255, 255, 255, .75);\n\n                &.active {\n                    color: #fff;\n                    background: rgba(0, 0, 0, .2);\n                }\n\n                &:hover {\n                    background: rgba(0, 0, 0, .1);\n                }\n            }\n        }\n    }\n\n    &[theme=\"white\"] {\n        background: rgba(0, 0, 0, .03);\n\n        &[direction=\"vertical\"] {\n            background: transparent;\n        }\n\n        novo-tab,\n        novo-tab-link {\n            .novo-tab-link {\n                color: $dark;\n                opacity: 0.75;\n            }\n\n            &.active,\n            &.router-link-active {\n                color: $positive;\n\n                .novo-tab-link {\n                    color: $positive;\n                    opacity: 1;\n                }\n\n                .indicator {\n                    background: $positive;\n                }\n            }\n        }\n\n        &[type=\"button-bar\"] {\n            border: 2px solid $light;\n\n            novo-tab-button {\n                color: $dark;\n                opacity: 0.75;\n\n                &.active {\n                    color: $positive;\n                    background: rgba(0, 0, 0, .05);\n                    opacity: 1;\n                }\n\n                &:hover {\n                    opacity: 1;\n                }\n            }\n        }\n    }\n\n    &[direction=\"vertical\"] {\n        flex-direction: column;\n        width: auto;\n\n        novo-tab,\n        novo-tab-link {\n            display: flex;\n            width: 100%;\n            flex-direction: row;\n            align-items: center;\n            height: 50px;\n            min-width: 150px;\n            justify-content: space-between;\n            border-left: 2px solid $light;\n            border-right: 2px solid $light;\n            transition: all 120ms ease-in-out;\n\n            &:first-of-type {\n                border-top-right-radius: 3px;\n                border-top-left-radius: 3px;\n                border-top: 2px solid $light;\n            }\n\n            &:last-of-type {\n                border-bottom-right-radius: 3px;\n                border-bottom-left-radius: 3px;\n                border-bottom: 2px solid $light;\n            }\n\n            .novo-tab-link {\n                flex: 1;\n                width: 100%;\n                display: block;\n                max-width: 90%;\n                white-space: nowrap;\n                overflow: hidden;\n                text-overflow: ellipsis;\n                opacity: 0.75;\n            }\n\n            .indicator {\n                width: 2px;\n                height: 0;\n                display: block;\n                background: transparent;\n                transition: all 220ms ease-in-out;\n            }\n\n            &.active,\n            &.router-link-active {\n                background: rgba(0, 0, 0, .05);\n\n                .novo-tab-link {\n                    opacity: 1;\n                }\n\n                .indicator {\n                    height: 100%;\n                }\n            }\n\n            &:hover {\n                .novo-tab-link {\n                    opacity: 1;\n                }\n            }\n        }\n    }\n\n    &[router] {\n        novo-tab-link.active:not(.router-link-active) {\n            opacity: 0.65;\n\n            .indicator {\n                background: inherit;\n                width: inherit;\n            }\n        }\n    }\n}\n\n// Outlets\nnovo-nav-outlet {\n    display: block;\n\n    novo-nav-header {\n        display: block;\n        padding: 8px;\n        border: 1px solid #333;\n        margin: 8px;\n    }\n\n    novo-nav-content {\n        display: none;\n\n        &.active {\n            display: block;\n        }\n    }\n}\n","$padding-top: 10px;\n$padding-side: 15px;\n\nbutton {\n    &[theme] {\n        font-size: 1rem;\n        border: none;\n        background: transparent;\n        border-radius: 3px;\n        padding: $padding-top $padding-side;\n        text-transform: uppercase;\n        transition: all 200ms ease-in-out;\n\n        div.flex-wrapper {\n            display: flex;\n            flex-direction: row;\n            width: 100%;\n            align-items: center;\n        }\n\n        i {\n            font-size: 1em;\n        }\n\n        &:focus {\n            outline: none;\n        }\n\n        &[disabled] {\n            opacity: 0.5;\n            cursor: not-allowed;\n            pointer-events: none;\n        }\n        // Primary Themed Button\n        &[theme=\"primary\"] {\n            background: $positive;\n            color: #fff;\n            min-width: 120px;\n            text-align: left;\n\n            .flex-wrapper {\n                justify-content: space-between;\n            }\n\n            &[icon] {\n                padding: ($padding-top - 5px) ($padding-side - 10px) ($padding-top - 5px) $padding-side;\n\n                i {\n                    background: rgba(0, 0, 0, .15);\n                    padding: 5px;\n                    border-radius: 50%;\n                    height: 25px;\n                    width: 25px;\n                    display: inline-block;\n                    margin-left: 10px;\n                    line-height: 16px;\n\n                    &:before {\n                        vertical-align: baseline;\n                    }\n                }\n\n                &[icon=\"send\"] {\n                    i {\n                        &:before {\n                            margin-top: 1px;\n                            margin-left: -2px;\n                        }\n                    }\n                }\n            }\n\n            &:hover {\n                background: lighten($positive, 10%);\n            }\n\n            &:focus {\n                background: darken($positive, 5%);\n            }\n            // Loop Generic Colors (Primary, Success, Negative, Etc.)\n            @each $button,\n            $color in $colors {\n                &[color=\"#{$button}\"] {\n                    background: $color;\n\n                    &:hover {\n                        background: lighten($color, 10%);\n                    }\n\n                    &:focus {\n                        background: darken($color, 5%);\n                    }\n                }\n            }\n            &[color=\"white\"] {\n                background: $white;\n                color: $ocean;\n\n                &:hover {\n                    background: $background;\n                }\n\n                &:focus {\n                    background: darken($background, 5%);\n                }\n\n                i {\n                    background: rgba(0, 0, 0, .05);\n                }\n            }\n            // Loop Analytics Colors\n            @each $analytics,\n            $color in $analytics-colors {\n                &[color=\"#{$analytics}\"] {\n                    background: $color;\n\n                    &:hover {\n                        background: lighten($color, 10%);\n                    }\n\n                    &:focus {\n                        background: darken($color, 5%);\n                    }\n                }\n            }\n        }\n        // Secondadry Themed Button\n        &[theme=\"secondary\"] {\n            min-width: 110px;\n            text-align: left;\n            align-items: center;\n            background: #fff;\n            color: $positive;\n            border: 1px solid $positive;\n\n            .flex-wrapper {\n                justify-content: space-between;\n            }\n\n            &:hover {\n                box-shadow: 0 3px 7px rgba(0, 0, 0, .15), 0 1px 2px rgba(0, 0, 0, .15);\n            }\n\n            &:focus {\n                box-shadow: 0 1px 1px rgba(0, 0, 0, .05);\n            }\n\n            &[inverse] {\n                background: rgba(0, 0, 0, .25);\n                color: #fff;\n                border: 1px solid #fff;\n\n                &:hover {\n                    background: rgba(0, 0, 0, .35);\n                }\n\n                &:focus {\n                    background: rgba(0, 0, 0, .45);\n                }\n            }\n\n            &[icon] {\n                padding: ($padding-top - 6px) ($padding-side - 11px) ($padding-top - 6px) ($padding-side - 1px);\n\n                i {\n                    padding: 5px;\n                    border-radius: 50%;\n                    height: 25px;\n                    width: 25px;\n                    display: inline-block;\n                    margin-left: 10px;\n                    line-height: 16px;\n\n                    &:before {\n                        vertical-align: baseline;\n                    }\n                }\n\n                &[icon=\"collapse\"] {\n                    i {\n                        &:before {\n                        }\n                    }\n                }\n\n                &[icon=\"convert\"] {\n                    i {\n                        &:before {\n                        }\n                    }\n                }\n            }\n        }\n        // Dialoge Themed Button\n        &[theme=\"dialogue\"] {\n            background: transparent;\n            color: $positive;\n\n            .flex-wrapper {\n                justify-content: center;\n            }\n\n            &[icon] {\n                display: flex;\n                align-items: center;\n                justify-content: space-between;\n\n                i {\n                    padding: 5px;\n                    border-radius: 50%;\n                    height: 25px;\n                    width: 25px;\n                    display: inline-block;\n                    line-height: 16px;\n\n                    &:before {\n                        vertical-align: baseline;\n                    }\n                }\n\n                &[side=\"left\"] {\n                    padding: ($padding-top - 5px) $padding-side ($padding-top - 5px) ($padding-side - 10px);\n\n                    i {\n                        margin-right: 5px;\n                    }\n                }\n\n                &[side=\"right\"] {\n                    padding: ($padding-top - 5px) ($padding-side - 10px) ($padding-top - 5px) $padding-side;\n\n                    i {\n                        margin-left: 5px;\n                    }\n                }\n            }\n\n            &:hover {\n                background: lighten($light, 7%);\n            }\n\n            &:focus {\n                background: lighten($light, 2%);\n            }\n\n            &[inverse] {\n                &:hover {\n                    background: rgba(0, 0, 0, .2);\n                }\n\n                &:focus {\n                    background: rgba(0, 0, 0, .35);\n                }\n            }\n            // Loop Generic Colors (Primary, Success, Negative, Etc.)\n            @each $button,\n            $color in $colors {\n                &[color=\"#{$button}\"] {\n                    color: $color;\n\n                    i {\n                        color: $color;\n                    }\n                }\n            }\n            // Loop Analytics Colors\n            @each $analytics,\n            $color in $analytics-colors {\n                &[color=\"#{$analytics}\"] {\n                    color: $color;\n\n                    i {\n                        color: $color;\n                    }\n                }\n            }\n        }\n        // Standard Themed Icon\n        &[theme=\"standard\"] {\n            color: $dark;\n            background: $light;\n\n            .flex-wrapper {\n                justify-content: center;\n            }\n\n            &:hover {\n                box-shadow: 0 3px 7px rgba(0, 0, 0, .15), 0 1px 2px rgba(0, 0, 0, .15);\n            }\n\n            &:focus {\n                box-shadow: 0 1px 1px rgba(0, 0, 0, .05);\n            }\n            // Loop Generic Colors (Primary, Success, Negative, Etc.)\n            @each $button,\n            $color in $colors {\n                &[color=\"#{$button}\"] {\n                    color: $white;\n                    background: $color;\n                }\n            }\n            // Loop Analytics Colors\n            @each $analytics,\n            $color in $analytics-colors {\n                &[color=\"#{$analytics}\"] {\n                    color: $white;\n                    background: $color;\n                }\n            }\n\n            &[color=\"light\"],\n            &[color=\"white\"],\n            &[color=\"gray\"],\n            &[color=\"background\"],\n            &[color=\"off-white\"] {\n                color: $dark;\n            }\n        }\n        // Icon Themed Button\n        &[theme=\"icon\"] {\n            .flex-wrapper {\n                text-align: center;\n\n                i {\n                    width: 100%;\n                    text-align: center;\n                }\n            }\n\n            &[inverse] {\n                color: #fff;\n            }\n            width: 40px;\n            height: 40px;\n            padding: 10px;\n\n            &:hover {\n                background: rgba(0, 0, 0, .1);\n            }\n\n            &:focus {\n                background: rgba(0, 0, 0, .25);\n            }\n        }\n    }\n}\n","novo-toast {\n    display: flex;\n    position: relative;\n    background: $navy;\n    z-index: 9999;\n    color: $white;\n    padding: 15px;\n    box-shadow: 0 1px 3px rgba(0, 0, 0, .15);\n\n    &.show {\n        display: block;\n    }\n}\n\nnovo-toast.launched {\n    display: none;\n    position: fixed;\n}\n\nnovo-toast.toast-container {\n    display: flex;\n    justify-content: flex-start;\n    align-content: center;\n    align-items: center;\n}\n\nnovo-toast.toast-container > .toast-icon {\n    display: flex;\n    justify-content: center;\n    align-content: center;\n    align-items: center;\n    float: left;\n    width: 32px;\n    height: 32px;\n    background: rgba(0, 0, 0, 0.15);\n    border-radius: 3px;\n    margin-right: 15px;\n\n    i {\n        position: relative;\n        top: 0;\n        font-size: 16px;\n        line-height: 16px;\n    }\n}\n\nnovo-toast.toast-container .toast-content {\n    float: left;\n    width: calc(100% - 47px);\n\n    h5 + p {\n        top: 0;\n    }\n\n    p {\n        position: relative;\n        top: -1px;\n    }\n}\n\nnovo-toast .toast-content > h5 {\n    display: block;\n    color: $white;\n    margin: 0 0 5px;\n    font-size: 14px;\n}\n\nnovo-toast:not(.default) .toast-content > h5 {\n    color: $white;\n}\n\nnovo-toast .toast-content > p {\n    font-size: 14px;\n    line-height: 20px;\n    margin: 0;\n    max-width: inherit;\n\n    &.message-only {\n        top: 0;\n        font-size: 18px;\n        line-height: 22px;\n    }\n}\n\nnovo-toast.danger,\nnovo-toast.info,\nnovo-toast.success,\nnovo-toast.warning {\n    i,\n    p {\n        color: $white;\n    }\n}\n\nnovo-toast.success {\n    background: $grass;\n}\n\nnovo-toast.info {\n    background: $ocean;\n}\n\nnovo-toast.warning {\n    background: $sunflower;\n}\n\nnovo-toast.danger {\n    background: $grapefruit;\n}\n\nnovo-toast {\n    @each $analytics, $color in $analytics-colors {\n        &.#{$analytics} {\n            background: $color;\n        }\n    }\n    @each $entity, $color in $entity-colors {\n        &.#{$entity} {\n            background: $color;\n        }\n    }\n\n    &.text {\n        background: $dark;\n    }\n\n    &.background {\n        background: $off-white;\n\n        .toast-content > h5 {\n            color: $dark !important;\n        }\n\n        i,\n        p {\n            color: $dark;\n        }\n    }\n\n}\n\nnovo-toast.fixedBottom,\nnovo-toast.fixedTop {\n    left: 0;\n    right: 0;\n}\n\nnovo-toast.fixedTop {\n    top: -100%;\n    padding-top: 20px;\n    @include ease( 'all', 'swift-ease-in-out');\n\n    &.animate {\n        padding-top: 20px;\n        top: -6px;\n    }\n}\n\nnovo-toast.fixedBottom {\n    bottom: -100%;\n    padding-bottom: 20px;\n    @include ease( 'all', 'swift-ease-in-out');\n\n    &.animate {\n        padding-bottom: 20px;\n        bottom: -6px;\n    }\n}\n\nnovo-toast[class*=\"growl\"] {\n    border-radius: 3px;\n    max-width: 350px;\n\n    .toast-content h5 {\n        display: block;\n        color: #fff;\n        margin: 0 0 5px;\n        font-size: 14px;\n    }\n\n    .toast-content p {\n        font-size: 14px;\n        width: 100%;\n    }\n}\n\nnovo-toast.growlBottomLeft,\nnovo-toast.growlTopLeft {\n    left: -100%;\n    right: auto;\n    width: calc(100% - 30px);\n    @include ease( 'all', 'swift-ease-in-out');\n\n    &.animate {\n        left: 15px;\n    }\n}\n\nnovo-toast.growlTopLeft,\nnovo-toast.growlTopRight {\n    top: 15px;\n}\n\nnovo-toast.growlBottomLeft,\nnovo-toast.growlBottomRight {\n    bottom: 15px;\n}\n\nnovo-toast.growlBottomRight,\nnovo-toast.growlTopRight {\n    right: -100%;\n    left: auto;\n    width: calc(100% - 30px);\n    @include ease( 'all', 'swift-ease-in-out');\n\n    &.animate {\n        right: 15px;\n    }\n}\n\nnovo-toast.growlTopLeft,\nnovo-toast.growlTopRight {\n    @for $i from 2 through 100 {\n        &:nth-last-of-type(#{$i}) {\n            top: (25px * $i);\n            transform: scale(1-(1/25));\n            z-index: 100 - $i;\n            filter: brightness(1+(1/7));\n        }\n    }\n}\n\nnovo-toast.growlBottomLeft,\nnovo-toast.growlBottomRight {\n    @for $i from 2 through 100 {\n        &:nth-last-of-type(#{$i}) {\n            bottom: (25px * $i);\n            transform: scale(1-(1/25));\n            z-index: 100 - $i;\n            filter: brightness(1+(1/7));\n        }\n    }\n}\n\nnovo-toast.growlBottomLeft,\nnovo-toast.growlBottomRight,\nnovo-toast.growlTopLeft,\nnovo-toast.growlTopRight {\n    &:last-of-type {\n        box-shadow: 0 1px 3px rgba(0, 0, 0, .5);\n    }\n}\n\nnovo-toast.embedded.fixedBottom,\nnovo-toast.embedded.fixedTop {\n    top: auto;\n    left: auto;\n    right: auto;\n    width: inherit;\n    margin: -1em -3em;\n\n    &.animate {\n        top: auto;\n    }\n}\n\nnovo-toast:not(.launched) {\n    animation: embedded-slide-in 200ms ease-in-out;\n}\n@keyframes embedded-slide-in {\n    from {\n        transform: translateY(-100%);\n        opacity: 0;\n    }\n\n    to {\n        transform: translateY(0);\n        opacity: 1;\n    }\n}\n\n//novo-toast + *:not(.toast-container) {\n//    margin-top: 60px;\n//}\n@media screen and (min-width: 350px) {\n    novo-toast[class*=\"growl-\"] {\n        min-width: 350px;\n    }\n\n    novo-toast.growlBottomLeft,\n    novo-toast.growlTopLeft {\n        left: -100%;\n        right: auto;\n        @include ease( 'all', 'swift-ease-in-out');\n\n        &.animate {\n            left: 15px;\n        }\n    }\n\n    novo-toast.growlBottomRight,\n    novo-toast.growlTopRight {\n        right: -100%;\n        left: auto;\n        @include ease( 'all', 'swift-ease-in-out');\n\n        &.animate {\n            right: 15px;\n        }\n    }\n}\n","novo-card-best-time {\n    label {\n        text-transform: uppercase;\n        display: block;\n        color: lighten($grey, 10%);\n        border-bottom: 1px solid lighten($grey, 10%);\n        padding-bottom: 3px;\n        margin-bottom: 10px;\n    }\n\n    .best-time {\n        display: flex;\n        flex-flow: row nowrap;\n\n        > i {\n            font-size: 35px;\n            margin-right: 10px;\n\n            &.morning {\n                &:before {\n                    vertical-align: inherit;\n                }\n            }\n\n            &.day {\n                &:before {\n                    vertical-align: inherit;\n                }\n            }\n\n            &.evening {\n                font-size: 30px;\n            }\n        }\n\n        .time {\n            font-weight: 500;\n            display: block;\n            margin-bottom: 5px;\n            padding-left: 5px;\n            text-transform: uppercase;\n\n            &.morning {\n                color: $aqua;\n            }\n\n            &.day {\n                color: $sunflower;\n            }\n\n            &.evening {\n                color: $lavender;\n            }\n        }\n\n        .days {\n            .day {\n                padding: 3px 5px;\n\n                &.active {\n                    background-color: $positive;\n                    color: $white;\n                    border-radius: 5px;\n                }\n            }\n        }\n    }\n}\n","novo-card-timeline {\n    .annotate {\n        display: inline-block;\n        background: $positive;\n        color: white;\n        font-size: 11px;\n        font-weight: 700;\n        width: 31px;\n        padding: 3px;\n        position: relative;\n        top: -26px;\n\n        &.one {\n            left: 0;\n\n            &:before {\n                left: 0;\n            }\n        }\n\n        &.smoosh {\n            display: block;\n            float: right;\n            top: -26px;\n            position: absolute;\n            left: calc(100% - 33px);\n        }\n\n        &.first.reverse {\n            display: block;\n            left: 0;\n            float: left;\n        }\n\n        &.last:before {\n            border-left: 5px solid transparent;\n            border-right: 5px solid $positive;\n            left: 23px;\n        }\n\n        &.last {\n            position: absolute;\n            left: calc(100% - 31px);\n        }\n\n        &.last:before {\n            border-left: 5px solid transparent;\n            border-right: 5px solid $positive;\n            left: 21px;\n        }\n\n        &.last.reverse {\n            left: calc(100% - 31px);\n\n            &:before {\n                border-left: 5px solid $positive;\n                border-right: 5px solid transparent;\n                left: 0;\n            }\n        }\n\n        &:before {\n            content: ' ';\n            width: 0;\n            height: 0;\n            border-bottom: 5px solid transparent;\n            border-right: 5px solid transparent;\n            border-left: 5px solid $positive;\n            left: 0px;\n            position: absolute;\n            margin-top: 16px;\n        }\n    }\n\n    .timeline-container {\n        padding-top: 28px;\n        margin-bottom: 15px;\n\n        .timeline-background {\n            width: 100%;\n            height: 4px;\n            background: #dddddd;\n\n            .timeline {\n                height: 4px;\n                background: $positive;\n                position: relative;\n\n                .hidden-width {\n                    display: none;\n                }\n            }\n        }\n    }\n}\n","novo-card-chart-donut {\n    aside {\n        display: inline-block;\n        position: relative;\n        text-align: center;\n        color: grey;\n        margin: 0 1em 2em;\n\n        &:before {\n            content: attr(data-name);\n            position: absolute;\n            width: 100%;\n            bottom: -2rem;\n        }\n\n        &:after {\n            content: attr(data-percent);\n            position: absolute;\n            width: 100%;\n            top: 3.7rem;\n            left: 0;\n            font-size: 2rem;\n            text-align: center;\n        }\n\n        svg {\n            width: 10rem;\n            height: 10rem;\n\n            &:nth-child(2) {\n                position: absolute;\n                left: 0;\n                top: 0;\n                transform: rotate(-90deg);\n\n                path {\n                    fill: none;\n                    stroke-width: 25;\n                    stroke-dasharray: 629;\n                    stroke: rgba(255, 255, 255, 0.9);\n                }\n            }\n        }\n    }\n}\n","// --------------------------------------------------\n// Extras\n// --------------------------------------------------\n@import \"./extras/best-time/CardBestTime\";\n@import \"./extras/timeline/CardTimeline\";\n@import \"./extras/donut-chart/CardDonutChart\";\n\nnovo-card {\n    .novo-card {\n        background-color: $white;\n        box-shadow: 0px 1px 4px rgba(#000, .15), 0px 2px 10px rgba(#000, .09);\n        margin-bottom: 20px;\n        position: relative;\n        border-radius: 3px;\n\n        header {\n            display: flex;\n            flex-flow: row nowrap;\n            align-items: center;\n            justify-content: space-between;\n            padding: 20px;\n\n            .title {\n                display: flex;\n                align-items: center;\n\n                i.bhi-move {\n                    color: $light;\n                    margin-right: 10px;\n                }\n\n                h3 {\n                    padding: 0;\n                }\n            }\n\n            .actions {\n                color: darken($light, 35%);\n                button {\n                    font-size: 1.2em;\n                }\n            }\n        }\n\n        main {\n            padding: 10px 20px 20px;\n            background-color: $white;\n\n            p.card-message {\n                padding: 20px 0;\n                max-width: inherit;\n                text-align: center;\n                line-height: 25px;\n                color: lighten($grey, 10%);\n\n                i {\n                    display: block;\n                    font-size: 24px;\n                    margin: 0 0 15px;\n                    color: lighten($grey, 20%);\n                }\n            }\n\n            novo-loading {\n                display: flex;\n                justify-content: center;\n            }\n        }\n\n        &.no-padding {\n            main {\n                padding: 0;\n            }\n        }\n\n        article.time-intervals {\n            .interval {\n                text-transform: capitalize;\n            }\n        }\n\n        footer {\n            padding: 10px 20px;\n            border-top: 1px solid lighten($grey, 20%);\n        }\n    }\n}\n","// TODO Refactor mixins and clean up scss\n\n@mixin enum-delay-child($map) {\n    $keys: map-keys($map);\n\n    @each $item in $keys {\n        $idx: index($keys, $item);\n\n        &:nth-of-type(#{$idx}) {\n            dot {\n                background-color: map-get($map, $item);\n                &.cycle {\n                    animation-delay: 100ms * $idx;\n                }\n            }\n        }\n    }\n}\n\nnovo-loading {\n    padding: 20px;\n\n    dot {\n        @include enumerate($dot-colors, 'background-color', 'map-val', '');\n        display: block;\n        border-radius: 50%;\n    }\n\n    // Line Theme Loader\n    &[theme=\"line\"] {\n        display: flex;\n        flex-direction: row;\n        font-size: 13px;\n\n        dot {\n            margin: 3px;\n            animation: jump 1600ms infinite;\n            @include delay($dot-colors, 70ms);\n            height: 13px;\n            width: 13px;\n\n        }\n    }\n\n    // Circle Theme Loader\n    &[theme=\"circle\"] {\n        display: block;\n        width: 100px;\n        height: 100px;\n        position: relative;\n\n        > div {\n            position: absolute;\n            top: 0;\n            right: 0;\n            bottom: 0;\n            left: 0;\n\n            // Spin animations\n            &.open {\n                animation: open-rotate 400ms ease-in-out;\n            }\n            &.cycle {\n                animation: cycle-rotate 10000ms infinite linear;\n            }\n            &.close {\n                animation: close-rotate 400ms ease-in-out;\n            }\n\n            > div {\n                height: 60px;\n                width: 10px;\n                top: 22%;\n                left: 46%;\n                position: absolute;\n                @include enum-delay-child($dot-colors);\n                @include enumerate($dot-colors, 'transform', 'rotate', 72deg);\n            }\n\n            dot {\n                display: block;\n                position: absolute;\n                height: 10px;\n                width: 10px;\n                margin: 3px;\n\n                // Dot animations\n                &.open {\n                    animation: open 400ms ease-in-out;\n                }\n                &.cycle {\n                    animation: circle-jump 1600ms infinite;\n                }\n                &.close {\n                    animation: close 400ms ease-in-out;\n                }\n            }\n        }\n    }\n\n    &[inverse] {\n        dot {\n            background-color: #fff !important;\n        }\n    }\n}\n\n@keyframes jump {\n    0% {\n        transform: translateY(0px)\n    }\n    30% {\n        transform: translateY(0px);\n        opacity: 1;\n    }\n    45% {\n        transform: translateY(-1.5em);\n        opacity: .5;\n    }\n    60% {\n        transform: translateY(.8em);\n        opacity: .95;\n    }\n    100% {\n        transform: translateY(0px);\n        opacity: 1;\n    }\n}\n\n@keyframes circle-jump {\n    0% {\n        transform: translateY(0px)\n    }\n    30% {\n        transform: translateY(0px);\n        opacity: 1;\n    }\n    45% {\n        transform: translateY(-.3em);\n        opacity: .5;\n    }\n    60% {\n        transform: translateY(.13em);\n        opacity: .95;\n    }\n    100% {\n        transform: translateY(0px);\n        opacity: 1;\n    }\n}\n\n@keyframes open {\n    0% {\n        transform: translateY(2em);\n        opacity: 0;\n    }\n    100% {\n        transform: translateY(0px);\n        opacity: 1;\n    }\n}\n\n@keyframes open-rotate {\n    0% {\n        transform: rotate(0deg);\n    }\n    75% {\n        transform: rotate(690deg);\n    }\n    100% {\n        transform: rotate(720deg);\n    }\n}\n\n@keyframes cycle-rotate {\n    0% {\n        transform: rotate(720deg);\n    }\n    100% {\n        transform: rotate(1080deg);\n    }\n}\n\n@keyframes close-rotate {\n    0% {\n        transform: rotate(720deg);\n    }\n    100% {\n        transform: rotate(360deg);\n    }\n}\n\n@keyframes close {\n    0% {\n        transform: translateY(0px);\n        opacity: 1;\n    }\n    100% {\n        transform: translateY(2em);\n        opacity: 0;\n    }\n}\n","// Function to loop through a SCSS map ($map) and set the map's key values to a\n// specific CSS attribute ($target) each time creating a new :nth-of-type rule at\n// the key's index.\n\n// For example see \"loading\" element. (BhLoading.scss)\n\n@mixin enumerate($map, $target, $prop, $val) {\n    $keys: map-keys($map);\n\n    @each $item in $keys {\n        $idx: index($keys, $item);\n        //@debug $idx;\n        //@debug $item;\n        @if $prop == 'map-val' {\n            &:nth-of-type(#{$idx}) {\n                #{$target}: map-get($map, $item);\n            }\n        }\n\n        @if $target == 'transform' {\n            &:nth-of-type(#{$idx}) {\n                #{$target}: #{'#{$prop}(#{$val * $idx})'};\n            }\n        }\n    }\n}\n\n// Animation Delay Function - multiplies $base-speed by key index;\n@mixin delay($map, $base-speed) {\n    $keys: map-keys($map);\n\n    @each $item in $keys {\n        $idx: index($keys, $item);\n\n        &:nth-of-type(#{$idx}) {\n            animation-delay: $base-speed * $idx;\n        }\n    }\n}\n","novo-select {\n    display: block;\n    //position: relative;\n    max-width: 180px;\n    min-width: 180px;\n    cursor: pointer;\n    font-size: 1rem;\n\n    &.active {\n        .novo-select-list {\n            position: absolute;\n            z-index: 2;\n            max-height: 200px;\n            min-width: 200px;\n            width: inherit;\n            overflow: auto;\n            padding: 8px 0 16px;\n            opacity: 1;\n        }\n    }\n\n    button {\n        display: flex;\n        justify-content: space-between;\n        align-items: center;\n        background-color: transparent;\n        border: none;\n        border-bottom: 1px solid rgba(0, 0, 0, 0.12);\n        color: rgba(0, 0, 0, 0.73);\n        height: 2.05rem;\n        position: relative;\n        text-align: left;\n        text-shadow: none;\n        width: 100%;\n        z-index: 1;\n        cursor: pointer;\n        text-transform: none;\n        padding: 0 10px 0 0;\n        font-size: 1rem;\n        &.empty {\n            color: #A9A9A9;\n        }\n        &:focus,\n        &:hover {\n            outline: none;\n        }\n        &:hover {\n            border-bottom: 1px solid lighten($dark, 45%);\n        }\n        &:focus {\n            border-bottom: 1px solid $positive;\n            i {\n                color: rgba(0, 0, 0, 0.73);\n            }\n        }\n        i {\n            font-size: .8em;\n            color: #A9A9A9;\n        }\n\n    }\n    .novo-select-list {\n        background-color: white;\n        cursor: default;\n        list-style: none;\n        line-height: 26px;\n        overflow: hidden;\n        margin: 0;\n        max-height: 0;\n        position: absolute;\n        padding: 0;\n        transform: translateY(-50%);\n        transition: all 0.15s cubic-bezier(0.35, 0, 0.25, 1);\n        width: 100%;\n        box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);\n        font-size: 1rem;\n        z-index: -1;\n        opacity: 0;\n        li {\n            cursor: pointer;\n            height: 48px;\n            margin: 0;\n            padding: 10px 16px;\n            box-sizing: border-box;\n            display: flex;\n            align-items: center;\n            justify-content: space-between;\n            color: darken($light, 45%);\n            font-size: 1rem;\n            span {\n                display: inline-block;\n            }\n            i {\n                height: 30px !important;\n                width: 15px !important;\n                border-radius: 0;\n                margin: 0 !important;\n                color: $positive;\n            }\n            &:focus,\n            &:hover {\n                background: lighten($light, 10%);\n                color: darken($light, 55%);\n            }\n            &.active {\n                color: darken($light, 55%);\n                font-weight: 500;\n            }\n        }\n        &:focus {\n            outline: none;\n        }\n    }\n\n    &[disabled] {\n        pointer-events: none;\n        button {\n            color: $grey;\n        }\n    }\n}\n","novo-dropdown {\n    display: block;\n    position: relative;\n\n    .mask {\n        position: fixed;\n        height: 100%;\n        width: 100%;\n        background: transparent;\n        top: 0;\n        left: 0;\n        z-index: 1;\n    }\n\n    button {\n        position: relative;\n        z-index: 0;\n    }\n\n    &.active {\n        .dropdown-container {\n            max-height: 500px;\n            overflow: auto;\n            padding: 8px 0 16px;\n            z-index: 2;\n            border-radius: 2px;\n            top: 100%;\n            margin-top: 5px;\n        }\n    }\n\n    .dropdown-container {\n        background-color: white;\n        cursor: pointer;\n        list-style: none;\n        line-height: 26px;\n        overflow: hidden;\n        margin: 0;\n        max-height: 0;\n        position: absolute;\n        padding: 0;\n        transform: translateY(0);\n        transition: all 0.15s cubic-bezier(0.35, 0, 0.25, 1);\n        width: 100%;\n        max-width: 180px;\n        min-width: 180px;\n        box-shadow: 0 3px 1px -2px rgba(0, 0, 0, .2), 0 2px 2px 0 rgba(0, 0, 0, .14), 0 1px 5px 0 rgba(0, 0, 0, .12);\n\n        list {\n\n            item {\n                cursor: pointer;\n                height: 48px;\n                margin: 0;\n                padding: 10px 16px;\n                box-sizing: border-box;\n                display: flex;\n                align-items: center;\n                justify-content: space-between;\n                color: darken($light, 45%);\n                span {\n                    display: inline-block;\n                }\n                i {\n                    height: 30px !important;\n                    width: 15px !important;\n                    border-radius: 0;\n                    margin: 0 !important;\n                    color: $positive;\n                }\n                &:focus,\n                &:hover {\n                    background: lighten($light, 10%);\n                    color: darken($light, 55%);\n                }\n                &.active {\n                    color: darken($light, 55%);\n                    font-weight: 500;\n                }\n            }\n        }\n    }\n    &[side=left] {\n        display: flex;\n        flex-direction: column;\n        align-items: flex-start;\n    }\n    &[side=right] {\n        display: flex;\n        flex-direction: column;\n        align-items: flex-end;\n        .dropdown-container {\n            right: 0px;\n        }\n    }\n\n}\n",".drawer {\n    display: inline;\n    .drawer-content {\n        position: fixed;\n        background-color: white;\n        box-shadow: 0 0 3px 1px rgba(0, 0, 0, 0.15), 1px 1px 17px rgba(0, 0, 0, 0.15);\n        transition: all 160ms ease-in-out;\n        &.left {\n            top: 0;\n            left: 0;\n            bottom: 0;\n            width: 250px;\n            transform: translate(-250px, 0);\n        }\n        &.right {\n            top: 0;\n            right: 0;\n            bottom: 0;\n            width: 250px;\n            transform: translate(250px, 0);\n        }\n        &.top {\n            top: 0;\n            left: 0;\n            right: 0;\n            height: 100px;\n            transform: translate(0, -100px);\n        }\n        &.bottom {\n            bottom: 0;\n            left: 0;\n            right: 0;\n            height: 100px;\n            transform: translate(0, 100px);\n        }\n    }\n    &.open {\n        .drawer-content {\n            &.left,\n            &.right,\n            &.top,\n            &.bottom {\n                transform: translate(0, 0);\n            }\n        }\n    }\n}\n","$switch-width: 36px !default;\n$switch-height: 24px !default;\n$switch-bar-height: 14px !default;\n$switch-thumb-size: 20px !default;\n@mixin novo-switch-style($thumb, $track) {\n    .novo-switch-thumb {\n        background-color: $light;\n    }\n\n    .novo-switch-bar {\n        background-color: lighten($light, 10%);\n    }\n\n    // Can make the checked version style different (same for now)\n    &[aria-checked=\"true\"] {\n        .novo-switch-thumb {\n            background-color: $thumb;\n        }\n\n        .novo-switch-bar {\n            background-color: $track;\n        }\n    }\n}\n\nnovo-switch {\n    display: flex;\n    align-items: center;\n    margin: 15px;\n    white-space: nowrap;\n    cursor: pointer;\n    outline: none;\n    user-select: none;\n    @include ease( 'all', 'swift-ease-in-out');\n\n    * {\n        box-sizing: border-box;\n    }\n\n    .novo-switch-container {\n        cursor: grab;\n        width: $switch-width;\n        height: $switch-height;\n        position: relative;\n        user-select: none;\n        margin-right: 8px;\n    }\n\n    &:not([disabled]) {\n        &.novo-switch-dragging .novo-switch-container,\n        .novo-switch-dragging {\n            cursor: grabbing;\n        }\n    }\n\n    .novo-switch-label {\n        border: 0 transparent;\n    }\n\n    .novo-switch-bar {\n        left: 1px;\n        width: $switch-width - 2px;\n        top: $switch-height / 2 - $switch-bar-height / 2;\n        height: $switch-bar-height;\n        border-radius: 8px;\n        position: absolute;\n    }\n\n    .novo-switch-thumb-container {\n        top: $switch-height / 2 - $switch-thumb-size / 2;\n        left: 0;\n        width: $switch-width - $switch-thumb-size;\n        position: absolute;\n        transform: translate3d(0, 0, 0);\n        z-index: 1;\n    }\n\n    &[aria-checked=\"true\"] .novo-switch-thumb-container {\n        transform: translate3d(100%, 0, 0);\n    }\n\n    .novo-switch-thumb {\n        position: absolute;\n        margin: 0;\n        left: 0;\n        top: 0;\n        outline: none;\n        height: $switch-thumb-size;\n        width: $switch-thumb-size;\n        border-radius: 50%;\n        box-shadow: $whiteframe-shadow-z1;\n\n        // todo\n        .bh-ripple-container {\n            position: absolute;\n            display: block;\n            width: auto;\n            height: auto;\n            left: -$switch-thumb-size;\n            top: -$switch-thumb-size;\n            right: -$switch-thumb-size;\n            bottom: -$switch-thumb-size;\n        }\n    }\n\n    &:not(.novo-switch-dragging) {\n        .novo-switch-bar,\n        .novo-switch-thumb,\n        .novo-switch-thumb-container {\n            @include ease( 'all', 'swift-ease-in-out');\n            transition-property: transform, background-color;\n        }\n\n        .novo-switch-bar,\n        .novo-switch-thumb {\n            transition-delay: 0.05s;\n        }\n    }\n\n    &[disabled] {\n        opacity: 0.5;\n        cursor: not-allowed;\n\n        .novo-switch-container {\n            cursor: not-allowed;\n        }\n    }\n}\n\nnovo-switch {\n    // Base color: $ocean\n    @include novo-switch-style($ocean, lighten($ocean, 18%));\n\n    // Loop through analytics colors and make a switch color class for each\n    @each $analytic, $color in $analytics-colors {\n        &.#{$analytic} {\n            @include novo-switch-style($color, lighten($color, 18%));\n        }\n    }\n\n    &.dark {\n        @include novo-switch-style($dark, darken($dark, 5%));\n    }\n\n    &.light {\n        @include novo-switch-style(darken($light, 3%), $light);\n    }\n\n    &[disabled] {\n        .novo-switch-thumb {\n            background-color: darken($light, 20%);\n        }\n\n        .novo-switch-bar {\n            background-color: $light;\n        }\n    }\n\n    &:focus {\n        .novo-switch-label:not(:empty) {\n            border: 1px dotted $light;\n        }\n    }\n}\n","header {\n    &.novo-header {\n        & > section.primary {\n            display: flex;\n            flex-direction: row;\n            align-items: center;\n            justify-content: space-between;\n            padding: 15px 25px;\n\n            h1 {\n                font-size: 1.5em;\n                margin: 0;\n                display: inline-block;\n                font-weight: 400;\n                max-width: 70%;\n                overflow: hidden;\n                white-space: nowrap;\n                text-overflow: ellipsis;\n            }\n\n            utils {\n                display: flex;\n                align-items: center;\n                justify-content: flex-start;\n            }\n        }\n        @each $analytics, $color in $analytics-colors {\n            &[theme=\"#{$analytics}\"] {\n                background: $color;\n                color: #fff;\n            }\n        }\n        @each $entity, $color in $entity-colors {\n            &[theme=\"#{$entity}\"] {\n                background: $color;\n                color: #fff;\n            }\n        }\n        @each $basic, $color in $colors {\n            &[theme=\"#{$basic}\"] {\n                background: $color;\n                color: #fff;\n            }\n        }\n        &[theme=\"white\"],\n        &[theme=\"off-white\"],\n        &[theme=\"light\"] {\n            color: $dark;\n        }\n    }\n}\n","// App Container Sizing Reset\nhtml {\n    height: 100%;\n    width: 100%;\n\n    body {\n        height: 100%;\n        width: 100%;\n\n        demo-app {\n            height: 100%;\n            width: 100%;\n\n        }\n    }\n}\n\n*, *:after, *:before {\n    box-sizing: border-box;\n}\n","// App general body styles\nhtml {\n    margin: 0;\n    padding: 0;\n\n    body {\n        margin: 0;\n        padding: 0;\n        position: relative;\n        overflow: scroll;\n        background: $white;\n\n        demo-app {\n            height: 100%;\n            width: 100%;\n            box-sizing: border-box;\n            display: block;\n        }\n    }\n}\n","/* Contains all Design Page /Component Demo Page SCSS imports\nstarting at line 82. >>\n=============================================================\n*/\n// Main content container\nmain.main-content {\n    position: relative;\n    min-height: 300px;\n    margin: 0 0 0 300px;\n\n    // Container class to center content\n    .container {\n        padding: 70px 5.5rem;\n        margin: 0 auto;\n\n        h1 {\n            small {\n                font-size: 14px;\n                vertical-align: middle;\n            }\n        }\n    }\n    // Contains component demos\n    .example {\n        margin-top: 25px;\n        border: 1px solid #E9EAEA;\n        border-top-right-radius: 3px;\n        border-top-left-radius: 3px;\n        border-bottom: none;\n        max-width: 53em;\n        box-sizing: border-box;\n\n        > * {\n            border-top-right-radius: 3px;\n            border-top-left-radius: 3px;\n        }\n    }\n    // Contains code snippet\n    pre {\n        margin: 0 0 35px;\n        max-width: 53em;\n        box-sizing: border-box;\n\n        code {\n            width: 100%;\n            display: block;\n            box-sizing: border-box;\n            border-bottom-left-radius: 3px;\n            border-bottom-right-radius: 3px;\n            line-height: 1.5em;\n            padding: 15px 25px;\n\n            &.hljs {\n                background: #F9F9F9 !important;\n            }\n        }\n    }\n    // DESIGN STYLES\n    header {\n        &.design {\n            display: flex;\n            flex-direction: row;\n            justify-content: space-between;\n\n            > * {\n                width: 50%;\n            }\n        }\n    }\n\n    section {\n        &.design {\n            background: $white;\n        }\n\n        > aside {\n            background: $background;\n            padding: 10px 20px;\n            border-radius: 3px;\n            display: inline-block;\n            border: 2px solid darken($background, 2%);\n\n            h6 {\n                color: $mint;\n                margin-bottom: 5px;\n            }\n\n            &.design-principle {\n                h6 {\n                    border-bottom: 1px solid darken($background, 5%);\n                    padding-bottom: 8px;\n\n                    span {\n                        font-weight: 300;\n                    }\n                }\n\n                .hint {\n                    display: block;\n                    color: darken($background, 50%);\n                    margin: 5px 0;\n                    padding: 10px;\n                    font-size: 0.9em;\n                    border-radius: 3px;\n                    background: darken($background, 5%);\n                    border: 1px solid darken($background, 10%);\n\n                    i {\n                        margin-right: 10px;\n                    }\n\n                    > span {\n                        display: block;\n                    }\n\n                    .link {\n                        display: block;\n                        margin-top: 10px;\n                        font-weight: 400;\n\n                        strong {\n                            font-weight: 600;\n                        }\n                    }\n                }\n            }\n        }\n    }\n\n    .accepted,\n    .not-accepted {\n        display: block;\n        margin-top: 15px;\n    }\n\n    .not-accepted {\n        color: $negative;\n    }\n\n    .accepted {\n        color: $success;\n    }\n    // Custom page element initial display reset\n    home,\n    layout,\n    typography,\n    iconography,\n    color {\n        display: block;\n\n        & > header {\n            background: $off-white;\n        }\n\n        & > section.design.container {\n            article {\n                margin: 80px 0;\n                // TODO: remove this\n            }\n        }\n    }\n    // DEMO STYLES\n    // Custom page element initial display reset\n    from-demo,\n    modal-demo,\n    switch-demo,\n    tooltip-demo,\n    buttons-demo {\n        display: block;\n    }\n}\n","// App side menu styles\nnav.main-nav {\n    position: fixed;\n    top: 0;\n    left: 0;\n    width: 300px;\n    height: 100vh;\n    color: $white;\n    background-color: #1B2127;\n    overflow-y: scroll;\n    padding: 0 20px 60px 20px;\n\n    // Logo Link\n    & > span.logo {\n        padding-top: 30px;\n        cursor: pointer;\n        text-align: center;\n        display: block;\n\n        & > * {\n            display: block;\n        }\n\n        h3, h5 {\n            padding: 0;\n            margin: 5px auto;\n        }\n\n        h5 {\n            color: rgba(#fff, .7);\n            font-weight: 500;\n            letter-spacing: .75px;\n        }\n\n        span.version {\n            text-align: center;\n            opacity: .5;\n        }\n    }\n\n    // Logo SVG\n    [data-name=\"novo-logo\"] {\n        width: 100%;\n        max-width: 100px;\n        margin: 0 auto 20px auto;\n        display: block;\n\n        .center-dot {\n            fill: $white;\n        }\n\n        .outer-ring {\n            fill: $bittersweet;\n            transform-origin: 50% 50%;\n            animation-duration: 20000ms;\n            animation-iteration-count: infinite;\n            animation-timing-function: linear;\n            animation-name: rotate-back;\n        }\n\n        .inner-ring {\n            fill: $bittersweet;\n            transform-origin: 50% 50%;\n            animation-duration: 20000ms;\n            animation-iteration-count: infinite;\n            animation-timing-function: linear;\n            animation-name: rotate;\n        }\n    }\n\n    // Bullhorn SVG\n    [data-name=\"bullhorn-text\"] {\n        width: 100%;\n        max-width: 150px;\n        margin: 0 auto 20px auto;\n        display: block;\n    }\n\n    // Start menu link styles\n    .menu-list {\n        position: relative;\n        margin: 20px 0 0 0;\n        padding: 0;\n        list-style: none;\n    }\n\n    .menu-item {\n        display: block;\n        margin: 0;\n        height: 2.5rem;\n    }\n\n    .menu-link {\n        display: block;\n        width: 90%;\n        height: 2.5rem;\n        line-height: 2.5rem;\n        text-align: left;\n        color: darken(#ACAEB1, 4%);\n        transition: color 0.3s;\n        font-size: 1.05em;\n        font-weight: 400;\n        cursor: pointer;\n        user-select: none;\n        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);\n        padding-left: 15px;\n    }\n\n    .menu-link:hover,\n    .menu-link:focus {\n        outline: none;\n        color: #fff;\n    }\n\n    .menu-item .router-link-active.menu-link {\n        color: #fff;\n        transition: color 0.5s;\n    }\n\n    .menu-section-header {\n        display: block;\n\n        span {\n            height: 2.5rem;\n            line-height: 2.5rem;\n            display: block;\n            text-transform: uppercase;\n            font-size: .9em;\n            font-weight: 500;\n            letter-spacing: .3px;\n            opacity: .25;\n        }\n    }\n\n    .menu-line {\n        position: absolute;\n        left: 100%;\n        top: 0;\n        height: 2.5rem;\n        width: 4px;\n        pointer-events: none;\n        background: $bittersweet;\n        transition: transform 0.5s;\n        transition-timing-function: cubic-bezier(1, 0.01, 0, 1.22);\n    }\n\n    @for $i from 1 through 30 {\n        $number: ($i - 1);\n        .menu-item:nth-child(#{$i}).current ~ .menu-line {\n            transform: translate3d(0, percentage($number), 0);\n        }\n    }\n}\n\n@-webkit-keyframes rotate {\n    from {\n        -webkit-transform: rotate(0deg);\n    }\n    to {\n        -webkit-transform: rotate(360deg);\n    }\n}\n\n@-webkit-keyframes rotate-back {\n    from {\n        -webkit-transform: rotate(360deg);\n    }\n    to {\n        -webkit-transform: rotate(0deg);\n    }\n}\n","/* Base16 Atelier Plateau Light - Theme */\n/* by Bram de Haan (http://atelierbram.github.io/syntax-highlighting/atelier-schemes/plateau) */\n/* Original Base16 color scheme by Chris Kempson (https://github.com/chriskempson/base16) */\n\n/* Atelier-Plateau Comment */\n.hljs-comment,\n.hljs-quote {\n    color: #655d5d;\n}\n\n/* Atelier-Plateau Red */\n.hljs-variable,\n.hljs-template-variable,\n.hljs-attribute,\n.hljs-tag,\n.hljs-name,\n.hljs-regexp,\n.hljs-link,\n.hljs-name,\n.hljs-selector-id,\n.hljs-selector-class {\n    color: #ca4949;\n}\n\n/* Atelier-Plateau Orange */\n.hljs-number,\n.hljs-meta,\n.hljs-built_in,\n.hljs-builtin-name,\n.hljs-literal,\n.hljs-type,\n.hljs-params {\n    color: #b45a3c;\n}\n\n/* Atelier-Plateau Green */\n.hljs-string,\n.hljs-symbol,\n.hljs-bullet {\n    color: #4b8b8b;\n}\n\n/* Atelier-Plateau Blue */\n.hljs-title,\n.hljs-section {\n    color: #7272ca;\n}\n\n/* Atelier-Plateau Purple */\n.hljs-keyword,\n.hljs-selector-tag {\n    color: #8464c4;\n}\n\n.hljs-deletion,\n.hljs-addition {\n    color: #1b1818;\n    display: inline-block;\n    width: 100%;\n}\n\n.hljs-deletion {\n    background-color: #ca4949;\n}\n\n.hljs-addition {\n    background-color: #4b8b8b;\n}\n\n.hljs {\n    display: block;\n    overflow-x: auto;\n    background: #f4ecec;\n    color: #585050;\n    padding: 0.5em;\n}\n\n.hljs-emphasis {\n    font-style: italic;\n}\n\n.hljs-strong {\n    font-weight: bold;\n}\n","$breakpoints: ('nill': 0px, 'phone': 320px, 'tween': 500px, 'tablet': 768px, 'desktop': 1024px) !default;\n\nnav.responsive-nav {\n    display: none;\n    padding: 0 20px;\n    width: 100%;\n\n    svg {\n        max-width: 90px;\n        display: inline-block;\n        margin: 0 auto;\n\n        .st0 {\n            fill: #3D464D;\n        }\n    }\n\n    strong {\n        text-transform: uppercase;\n        color: rgba(#3D464D, .5);\n        font-size: .9em;\n        letter-spacing: .3px;\n    }\n}\n\nbutton[name=\"open-menu\"] {\n    border: none;\n    background: transparent;\n    display: none;\n    font-size: 18px;\n    vertical-align: middle;\n\n    &:focus,\n    &:active {\n        outline: none;\n    }\n}\n\n@include media(\"<tween\") {\n    main.main-content {\n        .container {\n            padding: 70px 2.5rem;\n        }\n\n        header {\n            &.design {\n                > div {\n                    wid