include media(slice($conditions, 2)\n            ...) {\n                @content;\n            }\n        }\n    }\n}\n","color {\n    header {\n        transition: all 240ms ease-in-out;\n\n        h1 {\n            color: $grass;\n        }\n\n        h2,\n        p {\n            color: $dark;\n        }\n\n        h1,\n        h2,\n        p {\n            transition: color 240ms ease-in-out;\n            transition-delay: 100ms;\n        }\n        position: relative;\n\n        & > div {\n            z-index: 1;\n        }\n\n        .background-boxes {\n            position: absolute;\n            width: 50%;\n            height: 100%;\n            top: 0;\n            left: 50%;\n            right: 0;\n            display: flex;\n            flex-direction: row;\n            flex-wrap: wrap;\n            align-items: center;\n            z-index: 0;\n            justify-content: flex-end;\n            background: $background;\n            transition: background 240ms ease-in-out;\n\n            > * {\n                transition: all 120ms ease-in-out;\n                padding: 15px;\n                margin: 15px;\n                border-radius: 3px;\n\n                &:hover {\n                    transform: scale(1.7);\n                    border-radius: 50%;\n                }\n            }\n\n            @each $entity,\n            $color in $entity-colors {\n                .bb-#{$entity} {\n                    $randNum: random(20) + 25px;\n                    background: $color;\n                    width: $randNum;\n                    height: $randNum;\n                }\n            }\n\n            @each $analytics,\n            $color in $analytics-colors {\n                .bb-#{$analytics} {\n                    $randNum: random(20) + 25px;\n                    background: $color;\n                    width: $randNum;\n                    height: $randNum;\n                }\n            }\n        }\n    }\n\n    article {\n        &.color-bl